.project-lab {
    padding: 80px 80px; /* Increased padding for better space utilization */
    max-width: 1000px; /* Maintain maximum width for readability */
    margin: 150px auto; /* Centered with space at the top */
    background-color: rgb(255, 255, 255); /* Clean background */
    color: #333333; /* Dark text for contrast */
    border-radius: 12px; /* Soft rounded corners */
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
    font-family: 'Poppins', sans-serif; /* Elegant font */
    transition: background-color 0.3s ease, color 0.3s ease, box-shadow 0.3s ease; /* Smooth transitions */
}

.project-lab h1 {
    font-size: 3rem; /* Increased title size for better visibility */
    margin-bottom: 30px; /* Increased space below title */
    font-weight: 700; /* Bold title */
    text-align: center; /* Centered */
    color: #007AFF; /* Apple-like blue color */
}

.project-lab-description h2 {
    font-size: 2.25rem; /* Increased section heading size */
    margin-top: 20px; /* Increased top margin */
    margin-bottom: 20px; /* Increased bottom margin */
    font-weight: 600; /* Medium weight */
    text-align: left; /* Left aligned */
    color: #333333; /* Dark color for headings */
}

.project-lab-description p, 
.project-lab-description li {
    font-size: 1.125rem; /* Paragraph and list item text size */
    line-height: 1.8; /* Improved line height for readability */
    margin-bottom: 20px; /* Space below paragraphs and list items */
    text-align: left; /* Left aligned */
    color: #333333; /* Dark text color */
}

.project-lab-description ul {
    list-style-type: disc; /* Bullet points */
    padding-left: 40px; /* Indentation for list items */
}

.project-lab-description li {
    margin-bottom: 10px; /* Space below list items */
}

.project-lab img.project-image {
    width: 100%; /* Responsive image */
    max-width: 600px; /* Maximum width for images */
    margin: 10px 0; /* Margin around images */
    border-radius: 8px; /* Rounded corners for images */
}

/* Interactive hover effects */
.project-lab:hover {
    background-color: #f9f9f9; /* Lighter background on hover */
    box-shadow: 0 6px 25px rgba(0, 0, 0, 0.15); /* Deeper shadow on hover */
}

/* Dark Mode */
.dark-mode .project-lab {
    background-color: #333; /* Dark background */
    color: rgb(255, 255, 255); /* Light text for contrast */
}

.dark-mode .project-lab h1, 
.dark-mode .project-lab h2 {
    color: #61dafb; /* Light headings in dark mode */
}

.dark-mode .project-lab-description p, 
.dark-mode .project-lab-description li {
    color: rgb(255, 255, 255); /* Light text for paragraphs and list items */
}

.button-container {
    text-align: center; /* Center the buttons */
    margin-top: 40px; /* Space above the buttons */
}

.btn-website {
    display: inline-block; /* Keep the buttons in-line */
    background-color: #007AFF; /* Blue color similar to Apple’s design */
    color: white; /* White text for contrast */
    padding: 12px 24px; /* Padding for the button */
    font-size: 1.125rem; /* Button text size */
    font-weight: 500; /* Slightly bold text */
    border-radius: 8px; /* Rounded corners */
    margin: 10px; /* Space between buttons */
    text-decoration: none; /* Remove underline */
    transition: background-color 0.3s ease, transform 0.3s ease; /* Smooth hover effect */
}

.btn-website:hover {
    background-color: #005BBB; /* Darker blue on hover */
    transform: translateY(-2px); /* Slight lift on hover */
}

/* Responsive Design for Buttons */
@media (max-width: 768px) {
    .btn-website {
        font-size: 1rem; /* Adjust button text size for smaller screens */
        padding: 10px 20px; /* Adjust padding */
    }
}


/* Responsive Design */
@media (max-width: 768px) {
    .project-lab {
        padding: 60px 30px; /* Adjust padding for smaller screens */
    }

    .project-lab h1 {
        font-size: 2.5rem; /* Scale down title */
    }

    .project-lab-description h2 {
        font-size: 2rem; /* Scale down subheadings */
    }

    .project-lab-description p {
        font-size: 1rem; /* Adjust paragraph size for mobile */
        line-height: 1.5; /* Line height for mobile */
    }
}
