/* src/components/Projects.css */

/* Default Styles (Light Mode) */
.projects {
    padding: 100px 20px; /* Add more vertical space for overall section */
    background-color: rgba(255, 255, 255, 0); /* Keep it transparent for a clean look */
    color: #333333; /* Neutral dark color for text */
    text-align: center;
    font-family: 'Poppins', sans-serif; /* Modern, clean font */
}

.projects-card {
    background-color: #ffffff; /* Soft white for the card */
    padding: 40px; /* Padding for a spacious feel */
    border-radius: 20px; /* Rounded corners for softness */
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
    margin: 40px auto; /* Center the card */
    max-width: 1200px; /* Limit the width of the card */
}

.projects h1 {
    font-size: 3rem; /* Large title for impact */
    margin-bottom: 20px; /* Space below title */
    font-weight: 600; /* Semi-bold for emphasis */
    color: #007aff; /* Apple-like accent color */
}

.projects p {
    font-size: 1.2rem; /* Larger font size for description */
    margin-bottom: 40px; /* Space below paragraph */
    color: #555555; /* Softer gray for readability */
}

.project-list {
    display: grid; /* Grid layout for project cards */
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); /* Responsive grid */
    gap: 40px; /* Space between cards */
}

.project-card {
    background-color: #2e2e2e;
    border-radius: 20px;
    padding: 30px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.5);
    transition: transform 0.2s;
    text-align: left;
    display: flex;
    flex-direction: column; /* Ensure the card has a column layout */
    justify-content: space-between; /* Space between content and the buttons */
}

.project-card h2 {
    font-size: 1.5rem;
    margin-bottom: 15px;
    font-weight: 500;
    color: #ffffff;
}

.project-card p {
    font-size: 1rem;
    margin-bottom: 25px;
    color: #d3d3d3;
    flex-grow: 1; /* Make the description take up remaining space */
}

.project-card .cta-button-wrapper {
    margin-top: 20px; /* Add some spacing between content and buttons */
    display: flex;
    gap: 15px; /* Space between the buttons */
    justify-content: flex-start; /* Align buttons to the left */
}

.project-card .cta-button {
    display: inline-block;
    padding: 10px 20px;
    font-size: 0.9rem;
    color: #2e2e2e;
    background-color: #ffffff;
    text-decoration: none;
    border-radius: 20px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
    transition: background-color 0.2s ease, box-shadow 0.2s ease, transform 0.2s ease;
}

.project-card .cta-button:hover {
    background-color: rgba(255, 255, 255, 0.8);
    transform: translateY(-2px);
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}
.dark-mode .projects {
    color: #e0e0e0; /* Light text color for dark mode */
}

.dark-mode .projects-card {
    background-color: #333; /* Dark background for the card */
}

.dark-mode .projects h1 {
    color: #61dafb /* Keep the accent color consistent */
}

.dark-mode .projects p {
    color: #cccccc; /* Softer gray for readability in dark mode */
}

.dark-mode .project-card {
    background-color: #454444; /* Darker gray for project cards */
}

.dark-mode .project-card h2 {
    color: #ffffff; /* White for card titles */
}

.dark-mode .project-card p {
    color: #e0e0e0; /* Light gray for descriptions */
}

.dark-mode .project-card .cta-button {
    color: #413e3e; /* Dark background for button text */
    background-color: #ffffff; /* White background for button */
}

/* Mobile responsiveness */
@media (max-width: 768px) {
    .projects h1 {
        font-size: 2.5rem; /* Smaller title for mobile */
    }

    .projects-card {
        padding: 30px; /* Adjust padding for smaller screens */
    }

    .project-card {
        padding: 20px; /* Adjust padding for smaller screens */
    }

    .project-card h2 {
        font-size: 1.4rem; /* Adjust font size for smaller screens */
    }

    .project-card p {
        font-size: 0.95rem; /* Adjust font size for smaller screens */
    }

    .cta-button {
        font-size: 0.9rem; /* Adjust button text size for mobile */
        padding: 8px 16px; /* Adjust padding for smaller screens */
    }
}
